<template>
 <div class="Message-Bar">
   <el-card class="Message-Box-Item">
     <div slot="header" class="clearfix">
       <span>最近消息</span>
     </div>
     <div v-for="o in 4" :key="o">
       {{'列表内容 ' + o }}
     </div>
   </el-card>
   <el-card class="Message-Box-Item">
     <div>
       {{ message[1].message }}
     </div>
   </el-card>
 </div>
</template>

<script>
import MessageBox from './message/MessageBox'
export default {
  name: 'MessageBar',
  data () {
    return {
      message: [
        {'id': 1, 'message': '这是一条消息'},
        {'id': 1, 'message': '这是一条消息'},
        {'id': 1, 'message': '这是一条消息'},
        {'id': 1, 'message': '这是一条消息'}
      ]
    }
  },
  components: {
    MessageBox: MessageBox
  }
}
</script>

<style scoped>
.Message-Bar{

}
.Message-Box-Item{
  width: 90%;
  margin: 30% 5%;
  border: white solid 1px;
  border-radius: 5px;
  background: rgba(245, 225, 214, .2);
  color: white;
}
</style>



