<template>
  <div class="index">

    <ul>
      <li class="card-item" v-for="i in 3" :key="i">
        <el-card class="box-card">
          <div slot="header">
            <span class="card-item-title">
              <router-link to="/" class="card-item-title-link">
                Nginx解决访问图片显示404，403问题
