<script>
export default {
  name: 'Index'
}
</script>

<style scoped>
.index{
}
.card-item{
  font-family: Helvetica Neue,Helvetica,PingFang SC,Tahoma,Arial,sans-serif;;
  width: 90%;
  margin: 10% auto;
}
.card-item-title{
  font-size: 26px;
}
.card-item-title-link{
  color: white;
  text-decoration: none;
}
.box-card{
  background: none;
  color: rgb(202,203,203);
  border: none;
  box-shadow: 0 0 5px rgba(202,203,203,0.6);
}
.box-card:hover{
  color: white;
  background: rgba(202,203,203, .4);
  box-shadow: 0 0 5px rgb(202,203,203);
}
.card-item-subTitle, .card-item-text{
  line-height: 2;
}
.card-item-subTitle{
  font-size: 12px;
}
.card-item-text{
  font-size: 14px;
}
</style>



